body {
  margin: 0;
  font-family: system-ui;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.wrapper {
	/*overflow: auto;*/

	padding-top: 15px;
	padding-bottom: 15px;
	padding-left: 15px;
	padding-right: 15px;
	background-color: white;
	box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.07);
	border-radius: 20px;
	max-width: 1200px;
	margin: 50px auto;
}

header {
	display: flex;
	justify-content: space-between;
}

.logoHeader {
	display: flex;
	align-items: center;
}

img {
	margin-right: 13px;
}

h3, p {
	margin: 0px;
}

.card {
	margin: 5px;
	margin-top: 20px;
	text-align: center;
	padding-left: 8px;
	border: 1px solid black;
	border-radius: 15px;
	width: 200px;
	transition: 200ms ease-in-out;
	font-weight: bold;
	font-size: 16px;
}

.card:hover {
	box-shadow: 0px 20px 30px rgba(0, 0, 0, 0.12);
	transform: translateY(-5px);
}

.favorite {
	position: absolute;
	padding-top: 12px;
	padding-left: 3px;
}

.button {
	cursor: pointer;
	padding-top: 10px;
	text-align: center;
}

.orderHeader {
	cursor: pointer;
}

/*.button:active {
	fill: green;
}*/

b {
	font-size: 16px;
}

.searchLine {
	box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.3);
	margin-top: 10px;
	padding: 6px;
	margin-bottom: 40px;
	border: 1px solid #f3f3f3;
	border-radius: 10px;
	font-size: 16px;
}

.side-cart-shadow {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 200%;
	background-color: rgba(0, 0, 0, 0.5);
	z-index: 1;
}


.side-cart {
	background-color: white;
	position: absolute;
	width: 400px;
	right: 0;
	/*min-height: 200%*/
	height: 100%;
	box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.4);
	overflow: auto;
	padding: 30px;

}

.cart-item {
	overflow: auto;
	margin-top: 40px;
	padding-bottom: 20px;
	border: 1px solid black;
	border-radius: 20px;
	transition: 200ms ease-in-out;
}

.cart-item:hover {
	box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.12);
	transform: translateY(-5px);
}

.cart-h2 {
	position: absolute;
	margin: 0;
	font-size: 30px;
	width: 50%;
}

.remove {
	position: absolute;
	padding-top: 12px;
	padding-left: 12px;
}

.cart-item-text {
	position: absolute;
	font-size: 22px;
	padding-left: 160px;
	margin-top: 20px;
	text-overflow: ellipsis; 
    width: 200px; 
    white-space: nowrap; 
    overflow:hidden;
}

.order-summ {
	font-weight: bold;
	margin-top: 130px;
	font-size: 24px;
}

.check-out {
	align-content: center;
	height: 60px;
	width: 300px;
	background-color: #ff7700;
	border-radius: 18px;
	border: 0;
	font-size: 20px;
	font-weight: 700;
}

.check-out:hover {
	background: #ff9438;
}

.check-out:active {
	background-color: #ff7700;
}

.cart-close-button {
	cursor: pointer;
	padding-left: 350px;
	padding-bottom: 50px;

}

.all-cards-list {
	display: grid;
	grid-template-columns: auto auto auto auto auto;
	overflow: auto;
}